import React from 'react';
import { Link } from 'react-router-dom';

export default function GameFlappyBird() {
  return (
    <div className="card">
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <h2 style={{ margin: 0 }}>Flappy Bird</h2>
        <Link className="week-btn" to="/games">↩ Retour</Link>
      </div>
      <p style={{ color: 'var(--muted)' }}>Bientôt disponible. En attendant, essayez le Morpion !</p>
    </div>
  );
}